import Image from 'next/image';
import React from 'react';
import Basket from '/public/static/img/Basket.svg';
import BasketActive from '/public/static/img/checkoutActive.svg';
import Favourite from '/public/static/img/Favourite.svg';
import FavouriteActive from '/public/static/img/favActive.svg';
import { useRouter } from 'next/router';
import routes from 'helpers/routes';
import styles from './Actions.module.scss';
import Tooltip from 'components/UI/Tooltip';

type Props = {
	isMobile: boolean;
	favoutieHandler: (id: number) => Promise<void>;
	token: string;
	isFavourite: boolean;
	handleSizesOpen: () => void;
	isTooltip: boolean;
	id: number;
	isInBasket: boolean;
	isSizeChoosing: boolean; // tooltip
};

const Actions = ({
	isMobile,
	favoutieHandler,
	token,
	id,
	isFavourite,
	isInBasket,
	isSizeChoosing,
	isTooltip,
	handleSizesOpen,
}: Props) => {
	const router = useRouter();

	return (
		<div className={styles.icons}>
			<Image
				alt="fav"
				onClick={() => (token ? favoutieHandler(id) : router.push(router.asPath + routes.signIn))}
				src={isFavourite ? FavouriteActive : Favourite}
				width={isMobile ? 20 : 24}
				height={isMobile ? 20 : 24}
				style={{
					maxWidth: '100%',
					height: 'auto',
				}}
			/>
			<Tooltip withTriangle text="Товар добавлен" isOpen={isTooltip}>
				<Image
					alt="checkout"
					onClick={handleSizesOpen}
					src={isInBasket || isSizeChoosing ? BasketActive : Basket}
					width={isMobile ? 20 : 24}
					height={isMobile ? 20 : 24}
					className={styles.imageCheckout}
					style={{
						maxWidth: '100%',
						height: 'auto',
					}}
				/>
			</Tooltip>
		</div>
	);
};

export default Actions;
