import React from 'react';
import styles from './Colors.module.scss';

type Props = {
	colors?: string[];
	isMobile: boolean;
	activeColor?: string;
	handleSetActiveColor: (event: React.MouseEvent<HTMLButtonElement>) => void;
};

const Colors = ({ colors, isMobile, handleSetActiveColor, activeColor }: Props) => {
	const [isAtStart, setIsAtStart] = React.useState(true);
	const [isAtEnd, setIsAtEnd] = React.useState(false);

	const handleScroll = e => {
		const element = e.target;
		if (element.scrollLeft === 0) {
			setIsAtStart(true);
		} else {
			setIsAtStart(false);
		}
		if (element.scrollWidth - element.scrollLeft === element.clientWidth) {
			setIsAtEnd(true);
		} else {
			setIsAtEnd(false);
		}
	};
	return (
		<div className={isMobile ? styles.isHoverMobile : styles.isHover}>
			<div
				onScroll={handleScroll}
				className={`${!isAtEnd && !isAtStart && colors.length > 5 && styles.isGradient} 
                ${isAtStart && colors.length > 5 && styles.gradientRight} 
                ${isAtEnd && colors.length > 5 && styles.gradientLeft}`}
			>
				{colors.map(item => (
					<button
						key={item}
						id={item}
						onClick={handleSetActiveColor}
						className={isMobile ? styles.colorMobile : ''}
						style={{
							backgroundColor: item,
							border: activeColor === item ? '1px solid #1B2530' : 'none',
							cursor: 'pointer',
						}}
					/>
				))}
			</div>
		</div>
	);
};

export default Colors;
