import ActiveLink from 'components/ActiveLink';
import React from 'react';
import styles from './Cover.module.scss';
import routes from 'helpers/routes';
import ImagesProductBlock from '../ImagesBlock/ImagesProductBlock';
import Image from 'next/image';

type Props = {
	isMobile?: boolean;
	images: string[];
	id: number;
	secondImg: string;
	title: string;
	isHover: boolean;
};

const Cover = ({ isMobile, images, id, title, secondImg, isHover }: Props) => {
	return (
		<ActiveLink className={styles.imageLink} href={routes.productCard + id} isBlank>
			{isMobile ? (
				<ImagesProductBlock images={images} />
			) : (
				<Image
					alt={title}
					src={secondImg ? (isHover ? secondImg : images[0]) : images[0]}
					onLoadingComplete={img => (img.style.backgroundImage = 'none')}
					sizes="100vw"
					fill
					quality={80}
					style={{
						width: '100%',
					}}
				/>
			)}
		</ActiveLink>
	);
};

export default Cover;
