import React, { PropsWithChildren } from 'react';
import styles from './Tooltip.module.scss';
import shapeTriangle from '/public/static/img/shapeTriangle.svg';
import Image from 'next/image';

type Props = {
	withTriangle: boolean;
	text: string;
	isOpen: boolean;
};

const Tooltip = ({ withTriangle, text, children, isOpen }: PropsWithChildren<Props>) => {
	return (
		<>
			{isOpen && (
				<div className={styles.container}>
					<div className={styles.tooltip}>{text}</div>
					{withTriangle && <Image alt=" " src={shapeTriangle} className={styles.triangle} />}
				</div>
			)}
			{children}
		</>
	);
};

export default Tooltip;
