import React from 'react';
import styles from './Price.module.scss';
import { formattedPrice } from 'helpers/utils';

type Props = {
	price: number;
	oldPrice: number;
};

const Price = ({ price, oldPrice }: Props) => {
	return (
		<div className={styles.priceBlock}>
			<p>{formattedPrice(price)}</p>
			{oldPrice && <p className={styles.oldPrice}>{formattedPrice(oldPrice)}</p>}
		</div>
	);
};

export default Price;
