import ActiveLink from 'components/ActiveLink';
import routes from 'helpers/routes';
import React from 'react';
import styles from './Title.module.scss';

type Props = {
	id: number;
	title: string;
	isNovelties?: boolean;
};

const Title = ({ id, title, isNovelties }: Props) => {
	return (
		<ActiveLink href={routes.productCard + id} isBlank>
			<p className={`${isNovelties ? styles.isNovelties : ''}`}>{title}</p>
		</ActiveLink>
	);
};

export default Title;
