import React from 'react';

import styles from './ProductTile.module.scss';

import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { getProductItem } from 'store/catalog/ProductItemStore';
import FavoritesApi from 'api/favorites/FavoritesApi';
import { getFavoritesIds } from 'store/favorites/FavoritesStore';
import ym from 'react-yandex-metrika';
import AddProductApi from 'api/checkout/AddProductApi';
import { getCheckoutProduct } from 'store/product/CheckoutProductStore';
import { ProductDataModel } from 'api/product/models/ProductModel';
import { ApiResp } from 'api/product/ProductApi/types';
import { useAppSelector } from 'helpers/hooks/useAppSelector';
import Price from './views/Price';
import Colors from './views/Colors';
import Actions from './views/Actions';
import Title from './views/Title';
import Cover from './views/Cover';
import Sale from './views/Sale';
import Sizes from './views/Sizes';

const favoritesStore = new FavoritesApi();

type ProductTileTypes = {
	data: {
		id?: number;
		title: string;
		size?: string[];
		colors?: string[];
		colorNames?: string[];
		activeColor: string;
		price: number;
		oldPrice?: number;
		isSale?: boolean;
		persentSale?: number;
		images: string[];
		secondImg?: string | string;
	};
	withThree?: boolean;
	isNovelties?: boolean;
	productInCheckout?: boolean;
};

const addProductApi = new AddProductApi();

const ProductTile = ({ data, withThree, isNovelties, productInCheckout }: ProductTileTypes) => {
	const dispatch = useAppDispatch();

	const product = useAppSelector(state => state.product.product);
	const links = useAppSelector(state => state.breadcrumbsProduct.links);
	const favorites = useAppSelector(state => state.favorites.favoritesIds);
	const checkouts = useAppSelector(state => state.checkoutProduct.checkoutProduct);
	const token = useAppSelector(state => state.userData.token);
	const isMobile = useAppSelector(state => state.isMobile.isMobile);

	const [isHover, setIsHover] = React.useState(false);
	const [isSizeChoosing, setIsSizeChoosing] = React.useState(false);
	const [activeColor, setActiveColor] = React.useState<{ hex: string; id: number | null }>({
		hex: data?.activeColor,
		id: data?.id,
	});
	const [isTooltip, setIsTooltip] = React.useState(false);

	const isHoverHandler = (isEnter: boolean) => {
		if (isMobile) return;
		setIsHover(isEnter);
	};

	const handleSetActiveColor = (event: React.MouseEvent<HTMLButtonElement>) => {
		const newActiveColor = event.currentTarget.id;

		dispatch(getProductItem({ id: data?.id })).then(res => {
			const newActiveColorId = (res.payload as ApiResp<ProductDataModel>).data.colors?.find(
				colorKey => colorKey.hex === newActiveColor
			)?.id;
			setActiveColor({ hex: newActiveColor, id: newActiveColorId });
		});
	};

	const handleSizeChangeOpen = () => {
		dispatch(getProductItem({ id: activeColor.id })).then(() => {
			setIsSizeChoosing(true);
		});
	};

	const showTooltip = React.useCallback(() => {
		setIsTooltip(prev => !prev);
		setTimeout(() => {
			setIsTooltip(prev => !prev);
		}, 4000);
	}, []);

	const handleAddToBasket = async (id: number) => {
		const response = await addProductApi.addProduct({ id });
		if (response.data.message === 'success') {
			if (process.env.NEXT_PUBLIC_IS_PRODUCTION === 'true') {
				(window as any).dataLayer.push({
					ecommerce: {
						currencyCode: 'RUB',
						add: {
							products: [
								{
									id: product.id,
									name: product.name,
									category: links?.map(item => item.name).join('/'),
									price: product.price,
									variant: product.currentColor,
								},
							],
						},
					},
				});
			}
			showTooltip();
			setIsSizeChoosing(false);
			dispatch(getCheckoutProduct({ token }));
			if (process.env.NEXT_PUBLIC_YANDEX_METRIKA_ID) {
				ym('reachGoal', 'to-cart');
			}
		}
	};

	const favClickHandler = async (id: number) => {
		const isFav = favorites.find(item => item === data?.id);
		const response = isFav ? await favoritesStore.deleteFavorites({ id }) : await favoritesStore.addToFavorites({ id });
		if (response.data.message === 'ok') {
			dispatch(getFavoritesIds({ token }));
			if (!isFav && process.env.NEXT_PUBLIC_YANDEX_METRIKA_ID) {
				ym('reachGoal', 'add-to-favorites');
			}
		}
	};

	React.useEffect(() => {
		// если открыто окно выбора размера у другого товара
		// и открываем новый,
		// то прошлый закрвыаем
		setIsSizeChoosing(false);
	}, [product?.id]);

	return (
		<div
			onMouseEnter={() => isHoverHandler(true)}
			onMouseLeave={() => isHoverHandler(false)}
			className={`${styles.Product} ${withThree ? styles.withThree : ''}`}
		>
			<span>
				<Cover
					isMobile={isMobile}
					isHover={isHover}
					id={data?.id}
					images={data?.images}
					secondImg={data?.secondImg}
					title={data?.title}
				/>
				{isSizeChoosing && (
					<Sizes
						closeSizeChoosing={() => setIsSizeChoosing(false)}
						handleAddToBasket={handleAddToBasket}
						sizes={product?.size}
						checkouts={checkouts}
						isMobile={isMobile}
					/>
				)}
				<Sale persentSale={data?.persentSale} />
			</span>
			<div className={styles.actions}>
				<div className={styles.upper_actions}>
					<Title isNovelties={isNovelties} title={data?.title} id={data?.id} />
					<Price price={data?.price} oldPrice={data?.oldPrice} />
				</div>
				<div className={styles.bottom_actions}>
					<Colors
						isMobile={isMobile}
						activeColor={activeColor.hex}
						colors={data?.colors}
						handleSetActiveColor={handleSetActiveColor}
					/>
					<Actions
						id={data?.id}
						isTooltip={isTooltip}
						handleSizesOpen={handleSizeChangeOpen}
						token={token}
						favoutieHandler={favClickHandler}
						isFavourite={favorites?.includes(data?.id)}
						isSizeChoosing={isSizeChoosing}
						isInBasket={checkouts?.includes(data?.id) || productInCheckout}
						isMobile={isMobile}
					/>
				</div>
			</div>
		</div>
	);
};

export default React.memo(ProductTile);
