import Image from 'next/image';
import React from 'react';

import styles from './ImagesProductBlock.module.scss';

type ImagesProductBlockTypes = {
	images: string[];
};

let isDraggable = false;

const ImagesProductBlock = ({ images }: ImagesProductBlockTypes) => {
	const [currentImg, setCurrentImg] = React.useState<number>(0);
	const [currentImgScroll, setCurrentImgScroll] = React.useState<number>(currentImg);
	const [left, setLeft] = React.useState<number>(0);
	const [drag, setDrag] = React.useState<boolean>(false);
	const [imagesList, setImagesList] = React.useState<string[]>(images);

	const imageRef = React.useRef<HTMLDivElement | null>(null);
	const scroll = React.useRef<HTMLDivElement>(null);

	const onTouchdownHandler = (event: React.TouchEvent<HTMLDivElement>) => {
		setDrag(true);
		setLeft(event.touches[0].pageX);
	};

	const onTouchEndHandler = (event: React.TouchEvent<HTMLDivElement>) => {
		if (images.length === 1) return;
		if (isDraggable === true) return;
		setDrag(false);
		const target = event.currentTarget;
		if (left - event.changedTouches[0].pageX < 10 && left - event.changedTouches[0].pageX > -10) return;

		setCurrentImgScroll(left > event.changedTouches[0].pageX ? currentImgScroll + 1 : currentImgScroll - 1);

		if (!(imageRef && imageRef.current)) return;

		if (left > event.changedTouches[0].pageX) {
			isDraggable = true;
			setTimeout(() => {
				const newImageList = imagesList.slice(1).concat(imagesList[0]);
				target.style.transition = '0.3s';
				if (imageRef.current) target.style.transform = `translateX(-${imageRef.current.clientWidth}px)`;
				setTimeout(() => {
					setImagesList([newImageList[0], newImageList[0], ...newImageList.slice(2)]);
				}, 100);
				setTimeout(() => {
					target.style.transition = '0s';
					target.style.transform = `translateX(0px)`;
					setImagesList([...newImageList]);
					isDraggable = false;
				}, 300);
			}, 100);
		} else {
			setTimeout(() => {
				isDraggable = true;
				const newImageList = [imagesList[imagesList.length - 1]].concat(imagesList.slice(0, imagesList.length - 1));
				setImagesList([newImageList[1], newImageList[1], ...newImageList.slice(2)]);
				setTimeout(() => {
					target.style.transition = '0s';
					if (imageRef.current) target.style.transform = `translateX(-${imageRef.current.clientWidth}px)`;
				}, 50);
				setTimeout(() => {
					target.style.transition = '0.3s';
					target.style.transform = `translateX(0px)`;
					setImagesList([...newImageList]);
					isDraggable = false;
				}, 100);
			}, 100);
		}
	};

	const onTouchMoveHandler = (event: React.TouchEvent<HTMLDivElement>) => {
		if (isDraggable === true) return;
		if (drag) {
			if (imagesList.length - 1 === currentImg && left > event.targetTouches[0].pageX) return;
			if (0 === currentImg && left < event.targetTouches[0].pageX) return;
			setCurrentImg(left > event.targetTouches[0].pageX ? currentImgScroll + 1 : currentImgScroll - 1);
		}
	};

	return (
		<div ref={imageRef} className={styles.mobileImages}>
			<div className={styles.imagesScrollcontainer}>
				<div
					ref={scroll}
					onTouchStart={onTouchdownHandler}
					onTouchEnd={onTouchEndHandler}
					onTouchMove={onTouchMoveHandler}
					style={{ width: `calc(${imagesList.length} * 100%)` }}
					className={styles.imagesScrollBlock}
				>
					{imagesList.map((item, index) => {
						return (
							<Image
								alt="img"
								key={item + index}
								src={item}
								onLoadingComplete={img => (img.style.backgroundImage = 'none')}
								quality={80}
								width={375}
								height={542}
								style={{ width: `calc(100% / ${imagesList.length})` }}
							/>
						);
					})}
				</div>
			</div>
			{/*<div className={styles.dotList}>
				{[...Array(images.length)].map((item, index) => {
					return <div key={item} style={currentImg !== index ? { opacity: '0.2' } : { opacity: '1' }}></div>;
				})}
			</div>*/}
		</div>
	);
};

export default React.memo(ImagesProductBlock);
