import React from 'react';
import styles from './Sizes.module.scss';
import Image from 'next/image';
import closeSize from '/public/static/img/closeSize.svg';

type SizesProps = {
	sizes: { name: string; sizeId: number; id: number }[];
	isMobile: boolean;
	checkouts: number[];
	closeSizeChoosing: () => void;
	handleAddToBasket: (id: number) => void;
};

const Sizes = ({ sizes, isMobile, checkouts, handleAddToBasket, closeSizeChoosing }: SizesProps) => {
	const container = () => {
		return (
			<React.Fragment>
				<p className={styles.choose_size}>Выберите размер</p>
				<div className={styles.sizes}>
					{sizes.length &&
						sizes.map(size => (
							<div
								key={size.id}
								onClick={() => handleAddToBasket(Number(size.id))}
								style={{
									border: checkouts?.includes(size.id) && '1px solid #000',
									paddingLeft: size.name === 'One size' || size.name.includes('-') ? '8px' : '',
									paddingRight: size.name === 'One size' || size.name.includes('-') ? '8px' : '',
								}}
							>
								{size.name}
							</div>
						))}
				</div>
			</React.Fragment>
		);
	};
	return (
		<div className={styles.sizes_container}>
			{container()}
			{isMobile && (
				<Image className={styles.close} alt="X" src={closeSize} width={24} height={24} onClick={closeSizeChoosing} />
			)}
		</div>
	);
};

export default Sizes;
